html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', 'Roboto', Arial, sans-serif;
  background: radial-gradient(circle at 80% 10%, #f7c873 0%, #073058 40%, #0a4d9a 100%), 
              linear-gradient(120deg, #f7c87322 0%, #a7c7e7 100%);
  color: #2e3c5d;
  transition: background 0.3s;
}

.app-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.main-content {
  flex: 1;
}